import React from 'react'
import { NavLink } from 'react-router-dom'
import { useSelector } from 'react-redux';


const Navbar = () => {
    const DataTotal = useSelector((state) => state.handleCart);
    //console.log(DataTotal)
    return (
        <div className='w-full shadow-lg bg-white'>
            <div className="max-w-[1100px] flex justify-between items-center py-6 px-4 mx-auto">
                <NavLink to="/ShoppingCart" className="">
                    <h1 className='text-2xl font-semibold'><i className="fa-regular fa-gem text-3xl" style={{ color: 'var(--primary)', transform: "rotate(90deg)" }}></i>iamond lady</h1>
                </NavLink>
                <NavLink to="/ShoppingCart/Cart">
                    <div className='relative'>
                        <i className="fa fa-cart-shopping text-xl"></i>
                        <div style={{ background: 'var(--primary)' }} className='h-[20px] w-[20px] rounded-full absolute top-[-10px] left-[10px] text-xs text-white text-center flex justify-center items-center'>{DataTotal.length}</div>
                    </div>
                </NavLink>
            </div>
        </div>
    )
}
export default Navbar