import React, { useRef } from 'react'
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import { useDispatch } from 'react-redux';
import { addItem } from '../Redux/Action';
import { useInView } from 'framer-motion';
import { toast } from 'react-hot-toast';

const Cardlist = ({ id, image, title, price }) => {
    const ref = useRef(null)
    const isInView = useInView(ref, { once: true })
    const dispatch = useDispatch()
    const handleCart = () => {
        dispatch(addItem({ id, title, image, price, qty }))
        toast.success('Item successfully added to cart')
    }
    var qty = 0;
    return (
        <div className='md:w-[240px] md:h-[460px] w-[150px] h-[300px] rounded shadow-lg md:mx-3 mx-1 my-5 flex flex-col' ref={ref} style={{ marginTop: isInView ? '0px' : '300px', transition: '0.5s linear' }}>
            <img className='' src={image} />
            <div className='md:text-base py-1 px-4 text-xs'>
                <p className='truncate'>{title}</p>
                <p>Rs. {price}</p>
            </div>
            <div className='md:py-4 flex justify-around py-2 px-1'>
                <motion.span
                    initial={{ scale: 0.9 }}
                    whileHover={{ scale: 1 }}>
                    <Link className='md:text-base text-xs font-semibold rounded-sm text-center px-4 py-1 bg-gray-200 text-gray-700 flex items-center justify-center align-middle' onClick={handleCart} ><span className='md:block hidden'>add to </span>&nbsp;<span className='text-sm'><i className="fa-solid fa-cart-shopping"></i></span></Link>
                </motion.span>
                <Link to='/ShoppingCart/Cart' onClick={handleCart} className='md:text-base text-xs text-white font-semibold rounded-sm text-center px-4 py-1' style={{ background: 'var(--primary)' }}>Buy Now</Link>
            </div>
        </div>
    )
}

export default Cardlist
