import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from "./Pages/Home";
import Navbar from "./components/Navbar";
import Cart from "./components/Cart";

function App() {
  return (
    <BrowserRouter>
    <Navbar/>
      <Routes>
        <Route exact path='/ShoppingCart' element={<Home />}></Route>
        <Route exact path='/ShoppingCart/Cart' element={<Cart/>}></Route>
      </Routes>
    </BrowserRouter>
  );
}
export default App