import { combineReducers } from "redux";

const initialState = [];
export const handleCart = (state = initialState, action) => {
    const product = action.payload
    switch (action.type) {
        case 'addItem':
            const exPro = state.find((x) => x.id === product.id)
            if (exPro) {
                return state.map((x) =>
                    x.id === product.id ? { ...x, qty: x.qty + 1 } : x);
            } else {
                const product = action.payload;
                return [
                    ...state, {
                        ...product,
                        qty: 1,
                    }
                ]
            }

        case 'delItem':
            const exPro2 = state.find((x) => x.id === product.id)
            if (exPro2.qty === 1) {
                return state.filter((x) => x.id !== exPro2.id)
            } else {
                return state.map((x) =>
                    x.id === product.id ? { ...x, qty: x.qty - 1 } : x
                )
            }

        default:
            return state
    }
}

const rootReducers = combineReducers({
    handleCart
})

export default rootReducers;