import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { addItem, delItem } from '../Redux/Action';
import { Link } from 'react-router-dom'
import Confetti from '../components/Confetti'
import { toast, Toaster } from 'react-hot-toast';

const Cart = () => {
    const dispatch = useDispatch();
    const Data = useSelector((state) => state.handleCart);

    const handlePlus = (D) => {
        dispatch(addItem(D))
        console.log('xoo')
    }
    const handleMinus = (D) => {
        dispatch(delItem(D))
    }
    const [promo, setPromo] = useState('')
    const [discount, seDiscount] = useState(0)
    const handleText = (e) => {
        setPromo(e.target.value)
        if ((e.target.value).toUpperCase() == 'VISHAL143') {
            seDiscount(90)
        } else if ((e.target.value).toUpperCase() == 'DISC10') {
            seDiscount(10)
        } else if ((e.target.value).toUpperCase() == 'DISC5') {
            seDiscount(5)
        } else {
            seDiscount(0)
        }
    }
    const handleCheckOut = () => {
        toast('I am Sorry Babu',
            {
                icon: '',
                style: {
                    borderRadius: '6px',
                    background: '#FFF',
                    color: '#000',
                    padding:'10px',
                    fontSize:"24px"
                },
            }
        )
        setTurn((prev) => !prev)
    }
    var Total = 0
    const [turn, setTurn] = useState(false)
    return (
        <>
            {turn && <Confetti />}
            <div className='w-full'>
            <Toaster position="top-center" reverseOrder={true} />
                <div className="max-w-[1100px] my-10 px-5 mx-auto bg-white rounded py-6">
                    {Data.length > 0 ?
                        <>
                            <h1 className='text-xl font-semibold'>Your cart</h1>
                            <table className='w-full'>
                                <thead className='font-light cartTable m-4 text-gray-500'>
                                    <tr>
                                        <th className='border-b-2 py-4 text-start font-light'>Product</th>
                                        <th className='md:block hidden border-b-2 py-4 font-light text-end'>Quantity</th>
                                        <th className='border-b-2 py-4 font-light text-end'>Total</th>
                                    </tr>
                                    <tr><td colSpan='3'>&nbsp;</td></tr>
                                </thead>
                                <tbody className='text-gray-600 max-h-[60vh] overflow-scroll'>
                                    {Data.map((D, I) => {
                                        Total = Total + D.price * D.qty

                                        return (
                                            <tr key={I}>
                                                <td className='text-start border-b-2 py-4 '>
                                                    <div className='flex gap-3 justify-start items-center'>
                                                        <img className='rounded w-[100px]' src={D.image} />
                                                        <div className='flex flex-col'>
                                                            <p className='font-semibold'>{D.title}</p>
                                                            <p>{D.price}</p>
                                                            <div className='md:hidden justify-start flex items-center mt-5'>
                                                                <div style={{ background: '#F9F9F9' }} onClick={() => handleMinus(D)} className='cursor-pointer w-8 h-8 flex items-center  justify-center font-bold text text-red-600 rounded-l'><i className="fa-solid fa-minus"></i></div>
                                                                <input style={{ background: '#F9F9F9' }} className='w-8 h-8 border-0 outline-none text-center' type='text' value={D.qty} readOnly />
                                                                <div style={{ background: '#F9F9F9' }} onClick={() => handlePlus(D)} className='cursor-pointer w-8 h-8 flex items-center  justify-center font-bold text text-green-600 rounded-r'><i className="fa-solid fa-plus"></i></div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td className='text-end border-b-2 py-4 md:table-cell hidden'>
                                                    <div className='justify-end flex items-center'>
                                                        <div style={{ background: '#F9F9F9' }} onClick={() => handleMinus(D)} className='cursor-pointer w-8 h-8 flex items-center  justify-center font-bold text text-red-600 rounded-l'><i className="fa-solid fa-minus"></i></div>
                                                        <input style={{ background: '#F9F9F9' }} className='font-semibold w-8 h-8 border-0 outline-none text-center' type='text' value={D.qty} readOnly />
                                                        <div style={{ background: '#F9F9F9' }} onClick={() => handlePlus(D)} className='cursor-pointer w-8 h-8 flex items-center  justify-center font-bold text text-green-600 rounded-r'><i className="fa-solid fa-plus"></i></div>
                                                    </div>
                                                </td>
                                                <td className='text-end border-b-2 py-4 '>
                                                    <p className='text-lg font-bold'>Rs. {D.price * D.qty}</p>
                                                </td>
                                            </tr>
                                        )
                                    })}
                                    <span style={{ display: 'none' }}>{Total = (Total - ((Total * discount) / (100))).toFixed(0)}</span>
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <th className='py-6 text-end text-lg text-gray-600' colSpan='3'><b>Sub Total : <span className='text-xl'>Rs. {Total}.00</span></b></th>
                                    </tr>
                                </tfoot>
                            </table>
                            <p align='center'>Tax included. Shipping calculated at checkout.</p>
                            <div className='max-w-[400px] mx-auto p-5 my-5'>
                                <center>
                                    <input className='w-[60%] border p-3 rounded outline-none text-sm font-semibold my-1' type='search' value={promo} onChange={(e) => handleText(e)} /><br />
                                    <p className='text-xs text-green-600' style={{}}>{discount > 0 ? <>Promo code is applied.! <b>{discount}%</b> discount</> : ''}</p>
                                    <br />
                                </center>
                            </div>

                            {Total > 500 ?
                                <>
                                    <div className='max-w-[400px] mx-auto p-5 my-5' style={{ background: '#F9F9F9' }}>
                                        <div className='flex justify-between my-5'>
                                            <p>Partial Deposit</p><p className='font-semibold'>200.00 INR</p>
                                        </div>
                                        <div className='flex justify-between my-5'>
                                            <p>Remaining Balance</p><p className='font-semibold'>{Total - 200}.00 INR</p>
                                        </div>
                                        <p className='text-red-500 font-semibold my-5 text-center'>You need to pay remaining balance to delivery person</p>
                                    </div>
                                    <div className='flex flex-col gap-5 justify-center items-center my-10'>
                                        <button className='w-[300px] px-4 py-3 text-white font-semibold outline-none ' style={{ background: '#6200EA' }}>Partital Payment (200.00 INR) - Checkout</button>
                                        <button className='w-[300px] px-4 py-3 text-white font-semibold outline-none ' style={{ background: 'var(--primary)' }}>Full Payment ({Total}.00 INR) - Checkout</button>
                                    </div>
                                </> :
                                <center>
                                    <button className='max-w-[400px] px-4 py-3 text-white font-semibold mx-auto' style={{ background: 'var(--primary)' }} onClick={handleCheckOut}>Complete Payment ({Total}.00 INR) - Checkout</button>
                                </center>
                            }
                        </> :
                        <div className='flex justify-center items-center flex-col h-[40vh] gap-5'>
                            <h1 className='text-4xl font-bold'>Your cart is empty</h1>
                            <Link to='/ShoppingCart'><button className='outline-none w-[300px] px-4 py-3 text-white font-semibold text-2xl' style={{ background: 'var(--primary)' }}>Continue Shopping</button>   </Link>
                        </div>}
                </div>
            </div>
        </>)
}

export default Cart
